@import '../css/shared.scss';

.App {
    background-color: $-bgcolor;
    color: $-textColor;
}

.HeaderText {
    font-weight: bold;
    margin-left: 20px;
}

.SelectedArea {
    border: 2px solid $color5;
    overflow-y: auto;
    padding: 5px;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
}

.ProgressFC { //default
    background-color: $-bgcolor;
    color:$-textColor;
    
}

.RelicBox .DCommandFC {
    background-color: $piety-color;
    color: black;
}

.ProphetCommands .DCommandFC {
    background-color: $piety-color;
    color: black;
}

.DCommandFC:disabled {
    color: lightgrey;
}

.USDCommand .DCommandFC {
    background-color: $usd-color;
}

.PietyCommand .DCommandFC {
    background-color: $piety-color;
    color: black;
}

.GemsCommand .DCommandFC {
    background-color: $gems-color;
    color: black;
}


.GemsCommand .DCommandFC:disabled {
    background-color: $gems-color;
    color: gray;
}

.GemsDiv {
    color: $gems-color;
}


.ProgressFC > .InnerProgress { //default
    background-color: $color1;
}

.RelicBox {
    background-color: $color3;
    border: 2px solid $color4 ;
    padding: 5px;
    margin: 5px;
}

.ParitionerBox {
    background-color: $color3;
    border: 2px solid $color4 ;
    padding: 5px;
    margin: 5px;
}


.RelicBox > .ProgressFC {
    color: black;
    background-color: $color5;
}

.RelicBox > .ProgressFC > .InnerProgress {
    background-color: $piety-color;
}

.NavBar {
    background-color: $color3;
}

.TopBar {
    background-color: $color3;
    padding: 5px;
}

.PietyDiv {
    color: $piety-color;
}

.DevotionDiv {
    color: $devotion-color;
}

.USDDiv {
    color: $usd-color;
}

.HolinessDiv {
    color: $holiness-color;
}

.HolinessUI .DCommandFC {
    background-color: $holiness-color;
}

.HolinessButtonWrapper .DCommandFC:disabled {
    color: lightgray
}

.DivinityButtonWrapper .DCommandFC:disabled {
    color: green($color: #000000)
}

.ProphetUI {
    display: flex;
    justify-content: space-between;
    font-size: larger;
}

.HolyPowerWrapper {
    height: 100%;
}

.HolyPowerWrapper > .ProgressFC {
    background-color:  $color5;
    color: black;
}

.HolyPowerWrapper > .ProgressFC > .InnerProgress {
    background-color: $piety-color;
}

.ProphetCommands {
    display: flex;
    align-items: center;
}

.TopBar {
    display: flex;
    justify-content: space-between;
}

.FollowerDiv {
    border: 2px solid $color4;
    background-color: $color3;
    display: flex;
    flex-direction: column;
    margin: 5px;
}

.FollowerDiv .DCommandFC {
    background-color: $holiness-color;
}


.ParitionerBox .DCommandFC {
    background-color: $devotion-color;
}

.ParitionerButton .DCommandFC {
    background-color: $devotion-color;
}

.DevotionButtonWrapper .DCommandFC {
    background-color: $devotion-color;
}
.HolinessButtonWrapper .DCommandFC {
    background-color: $holiness-color;
}

.DivinityButtonWrapper .DCommandFC {
    background-color: $divinity-color;
    color: purple;
}

.FollowerDiv > * {
    margin-right: 10px;
}

.FollowerBox {
    display: flex;
    flex-wrap: wrap;
}

.RelicUI {
    display: flex;
    flex-direction: column;
}

.flexrow {
    display: flex;
    flex-direction: row;
}


.flexcolumn {
    display: flex;
    flex-direction: column;
}

.spacebetween {
    justify-content: space-between;
}

button:hover:not(:disabled) {
    box-shadow:  0 10px 20px 0 rgba(200,200,200,20);
}

button:active {
    background-color: white;
}

button {
    transition-duration: 0.4s;
}

.flexwrap {
    flex-wrap: wrap;
}

.DCommandFC:disabled {
    color:grey;
}

.flexgrow {
    flex-grow: 1;
}

.flexequal {
    flex-basis: 1px;
    flex-grow: 1;
}

.multbox {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: stretch;
}

.multbox > * {
    flex-grow: 1;
    flex-shrink: 1;
    
}

.relicboxrow {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
}

.relicboxrow > * {
    flex-basis: 1px;
    flex-grow: 1;
    white-space: nowrap;
}

.timebar {
    flex-basis: 1px;
    flex-grow: 1;
}

.GameTimeRow {
    
}

.flexend {
    justify-content: flex-end;
}

.itembox {
    border: 1px solid white;
    margin: 1px;
    padding: 1px;
}

.areabox {
    border: 1px solid white;
    margin: 1px;
    padding: 1px;
}

.godbox {
    padding: 2px;
    border: 2px solid white;
}

.max300width {
    max-width: 300px;
}