@import '../../css/shared.scss';

.TipFC {
    display: none;
}

:hover > * > .TipFC {
    display: initial;
}

.TipFC {
    border: 2px dashed $-textColor;
    background-color: $-bgcolor;
    color: $-textColor;
    z-index: 1;

    position: absolute;
    white-space: nowrap;
    margin: 5px;
    padding: 5px;
    pointer-events: none;
    
}

.TipFC > * {
    display: flex;
    white-space: pre-line;
}