@import '../../css/shared.scss';

.DCommandFC {
    background-color: $color3;
    color: inherit;
    padding: 1px;
    border: 2px solid $color5;
    margin: 2px;
} 

.biggerButtons {
    font-size: medium;
}

.PlainD {
    background-color: $color3;
    color: inherit;
    padding: 1px;
    border: 2px solid $color5;
    margin: 2px;
}

.DCommandFC:disabled {
    background-color: adjust-color($color:$color3, $red: -50, $green: -50, $blue: -50);
    color: gray;
    border: 2px solid gray;

}

.WarningBox {

}

.WarningOverlay {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3);

    display: flex;
    flex-direction: column;
    align-items: center;
}

.WarningPopup {
    margin-top: 20vh;
    padding: 1vh;
    height: 10vh;
    background-color: $color3;
    border: 2px solid $color5;
    
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}